<template>
  <div>
    <div class="form d-flex">
      <input class="input input--week" type="text" name="week" placeholder="Datum von/bis" v-on:keyup="changeWeek">
    </div>
    <div class="form d-flex flex-column">
      <select class="input input--game" name="game">
        <option value="">-</option>
        <option value="mtg">Magic</option>
        <option value="yugi">Yu-Gi-Oh!</option>
        <option value="fab">F&B</option>
      </select>
      <input class="input input--event" type="text" name="event" placeholder="Event">
      <input class="input input--price" type="text" name="price" placeholder="Eintritt">
      <input class="input input--date" type="text" name="date" placeholder="Tag">
      <input class="input input--time" type="text" name="time" placeholder="Uhrzeit">
    </div>
    <button class="button button--add-row" v-on:click="addRow">Zeile hinzufügen!</button>
    <Table msg="Gamers Finest Wochenplan"/>
  </div>
</template>

<script>
import Table from './components/Table.vue';

export default {
  name: 'App',
  components: {
    Table
  },
  methods: {
    changeWeek: function () {
      const weekInput = document.documentElement.querySelector('.input--week');
      const weekOutput = document.documentElement.querySelector('.timetable__subline');

      console.log('test');
      weekOutput.innerHTML = weekInput.value;
    },
    addRow: function () {
      const gameInput = document.documentElement.querySelector('.input--game');
      const eventInput = document.documentElement.querySelector('.input--event');
      const priceInput = document.documentElement.querySelector('.input--price');
      const dateInput = document.documentElement.querySelector('.input--date');
      const timeInput = document.documentElement.querySelector('.input--time');

      const tableBody = document.documentElement.querySelector('.table__body');
      const exampleRow = document.documentElement.querySelector('.example-row');

      const duplicateRow = exampleRow.cloneNode(true);

      duplicateRow.classList.remove('example-row');
      tableBody.appendChild(duplicateRow);

      let imageSource = exampleRow.dataset.srcMtg;
      if (gameInput.value === 'fab') {
        imageSource = exampleRow.dataset.srcFab;
      } else if (gameInput.value === 'yugi') {
        imageSource = exampleRow.dataset.srcYugi;
      }

      // REPLACE PLACEHOLDERS WITH CORRECT VALUES
      duplicateRow.innerHTML = duplicateRow.innerHTML.replace('https://i.imgur.com/MjoC1cj.png', imageSource);
      duplicateRow.innerHTML = duplicateRow.innerHTML.replace('#EVENT#', eventInput.value);
      duplicateRow.innerHTML = duplicateRow.innerHTML.replace('#PRICE#', priceInput.value);
      duplicateRow.innerHTML = duplicateRow.innerHTML.replace('#DATE#', dateInput.value);
      duplicateRow.innerHTML = duplicateRow.innerHTML.replace('#TIME#', timeInput.value);

      // RESET FIELDS
      eventInput.value = '';
      priceInput.value = '';
      dateInput.value = '';
      timeInput.value = '';
    }
  }
}
</script>

