<template>
  <div class="timetable__wrapper">
    <div class="timetable">
      <div class="timetable__header">
        <div class="timetable__header-left">
          <div class="logo__wrapper">
            <img class="logo__image" alt="Gamers Finest" src="https://i.imgur.com/ji62KZz.jpg">
          </div>
        </div>
        <div class="timetable__header-right">
          <h1 class="timetable__headline" >Card Arena Wochenplan</h1>
          <span class="timetable__subline"></span>
        </div>
      </div>
      <table class="timetable__table" cellspacing="0" cellpadding="0">
        <thead>
          <th>Event</th>
          <th></th>
          <th>Eintritt</th>
          <th>Tag</th>
          <th>Uhrzeit</th>
        </thead>
        <tbody class="table__body">

        </tbody>
      </table>

      <table class="example-table">
        <tr class="example-row" data-src-fab="https://i.imgur.com/GPDItWn.png" data-src-yugi="https://i.imgur.com/9Cd6HiL.png" data-src-mtg="https://i.imgur.com/MjoC1cj.png">
            <td><img class="game" src="https://i.imgur.com/MjoC1cj.png"></td>
            <td>#EVENT#</td>
            <td>#PRICE#</td>
            <td class="date">#DATE#</td>
            <td>#TIME#</td>
          </tr>
      </table>
    </div>
    <button class="export" v-on:click="exp">Export to png</button>
    <div class="output"></div>
  </div>
</template>

<script>
import domtoimage from 'dom-to-image';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  methods: {
    exp: function () {
      const timetable = document.documentElement.querySelector('.timetable');

      domtoimage.toPng(timetable)
      .then(function (dataUrl) {
        const img = new Image();
        img.src = dataUrl;

        const w = window.open('');
        w.document.write(img.outerHTML);
      })
      .catch(function (error) {
          console.error('oops, something went wrong!');
          console.error(error);
      });
    }
  }
}
</script>

